import React from 'react';
import styled from 'styled-components';
import info from '../data/info';

const CenteredHeader = styled.h4`
    text-align: center;
    margin-bottom: 1.25em;
`;

const ContainerRow = styled.li`
    min-height: 90px;

    h3 {
        text-decoration: underline;
    }
`;

const Grid = styled.div`
    width: 80%;
    height: 29.7cm;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-gap: 10px;
    grid-auto-rows: minmax(50px, auto);
`;

const DetailColumn = styled.div`
    grid-row: 1;
    grid-column: 2;
    /* background: green; */
    display: grid;
    text-align: left;
`;

const DateRow = styled.div`
    display: grid;
    grid-template-columns: 3fr 1fr;
    margin-top: 10px;
`;

const BulletRow = styled.li`
    list-style-type: square;
    margin-left: 18px;
`;

export default () => (
    <section>
        <CenteredHeader>Experiences</CenteredHeader>
        <ul>
            {info.skills.map((s, index) => (
                <ContainerRow key={index}>
                    <h3>{s.title}</h3>
                    <p>{s.description}</p>
                </ContainerRow>
            ))}
        </ul>
        <DetailColumn>
            {info.experiences.map((bodyItem, index) => (
                <div
                    key={index}
                    //  style={{ ...styles().body }}
                >
                    {bodyItem.content.map((contentItem, j) => (
                        <div
                            key={`bc${index}-${j}`}
                            style={{
                                // ...styles().bodyContentHeaders,
                                padding: 0,
                                marginBottom: 4,
                            }}
                        >
                            <DateRow>
                                <p style={{ background: 'red' }}>{contentItem.title}</p>
                                <p
                                    style={{
                                        background: 'blue',
                                        textAlign: 'right',
                                    }}
                                >
                                    {contentItem.rightContent}
                                </p>
                            </DateRow>
                            <ul>
                                {contentItem.bullets.map((bullet, index) => (
                                    <BulletRow key={index}>{bullet}</BulletRow>
                                ))}
                            </ul>
                        </div>
                    ))}
                </div>
            ))}
        </DetailColumn>
    </section>
);
